// æ ¹æ®æ•°æ®éšæœºæ˜¾ç¤ºå†…å®¹
function addChapter(data) {
    let isData = []
    if (type(data) === 'array' || type(data) === 'object') {
        isData = Array.from(data)
    } else {
        console.error('è¯·ä¼ å…¥ä¸€ä¸ªã€Œæ•°ç»„ã€æˆ–ã€Œä¼ªæ•°ç»„ã€')
    }
    // let isData = []
    let numberIsQuestions = []
    // isData.push(...data)
    let [parent, template, ulContent, title] = ['', '', '', '']
    // problemListï¼šè¦å±•ç¤ºçš„é—®é¢˜ï¼ŒgetQuestionListï¼šè¦å±•ç¤ºçš„æ‰€æœ‰æ•°æ®
    let [problemList, getQuestionList] = [[], []]
    let ques = numberIsQuestions

    isData.forEach((item, index) => {
        // æ¸…ç©ºä¹‹å‰çš„å†…å®¹
        ulContent = ''
        title = item.title
        //éšæœºæŠ½å–æŒ‡å®šæ•°é‡çš„é¢˜ç›®
        if (!!item.random) {
            let number = parseInt(item.random)
            problemList = randomArray(item.text, number).result
            getQuestionList[index] = {
                title,
                text: [...problemList]
            }
        } else {
            problemList = item.text
            getQuestionList[index] = {
                title,
                text: [...problemList]
            }
        }
        problemList.forEach(text => {
            ulContent += `<li>${text}</li>`
        })
        // å°†æŠ½å–åçš„é¢˜ç›®å’Œå¤§æ ‡é¢˜æ”¾å…¥æ¨¡æ¿ä¸­
        template = `
        <section class="part">
            <h2>${title}</h2>
            <ol>${ulContent}</ol>
        </section>
        `
        // å°†æ¨¡æ¿æ·»åŠ åˆ°ä¸€ä¸ªdivä¸­
        parent += template
    })
    app.innerHTML = parent
    return getQuestionList // è¿”å›å½“å‰éšæœºçš„åçš„é—®é¢˜
}

// è·å–å…ƒç´ 
function get_el(element) {
    return document.querySelector(element)
}

// éšæœºæŠ½å–é¢˜ç›®
function randomArray(array, number) {
    let isArray = Array.from(array)
    let result = []
    for (let i = 0; i < number; i++) {
        let single = Math.floor(Math.random() * isArray.length)
        result.push(isArray.splice(single, 1)[0])
    }
    return {
        result: result, // éšæœºæŠ½å–åçš„æ•°ç»„
        after: isArray  // åŸæ•°ç»„çš„æ‹·è´
    }
}

